
$primary-color: purple;
$secondary-color: red;
$accent-color: ;
$light-gray: #f3f3f3;
$charcoal: #a1a1a1;

body {
  // overflow: hidden; //stops scroll from moving above page in chrome/safari
  background-color: $light-gray;
}

h1,h2,h3,h4 {
  font-weight: normal;
}
.name {
  font-size: 22px;
  color: $primary-color; }

.genus {
  color: $secondary-color; }

.red {
  color: red;
  font-weight: bold;
}

.btn {
  padding: 5px 10px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 3px;
  // box-shadow: 0px 0px 10px 1px;
}

.btn-facebook {
  background-color: #4f6eae;
  color: white;
  border: none;
  border-radius: 3px;
  padding: 15px;
  width: 100%;
  cursor: pointer;
}

.btn:hover {
  background-color: $light-gray;
  cursor: pointer;
}

.btn:active {
  background-color: #d9d9d9;
}

.flleft {
  float: left;
}

.flright {
  float: right;
}

.hideMobile {
  display: none;
}

hr {
  border-color: $light-gray;
  border-top: none;
  border-left: none;
  margin: 15px 0;
}

hr.lastItem {
  border-width: 5px;
  border-color: $light-gray;
}

.w25 {
  width: 25%;
  // background-color: red;
}

.w50 {
  width: 50%;
  // background: blue;
}

.w75 {
  width: 75%;
}

/* ============================================
                  INDEX PAGE
============================================ */

nav {
  background: #fff;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid $light-gray;

/* =================== HAMBURGER MENU ================= */
  .ham-search-container {
    padding-left: 20px;

    .hamburger-icon {
      display: inline-block;
      cursor: pointer;
      margin-right: 4px;
      // padding-top: 2px;

      .nbar1, .nbar2, .nbar3 {
          width: 35px;
          height: 5px;
          background-color: #333;
          margin: 6px 0;
          transition: 0.4s;
          border-radius: 5px;
      }
    }

    .change .nbar1 {
        -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
        transform: rotate(-45deg) translate(-9px, 6px) ;
    }

    .change .nbar2 {
      opacity: 0;
    }

    .change .nbar3 {
        -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
        transform: rotate(45deg) translate(-8px, -8px) ;
    }

    .search-icon {
      font-size: 36px;
      // padding-left: 10px;
      cursor: pointer;
    }
  }


  .logo {
    text-align: center;
    padding: 20px 0;

    img {
      height: 30px;
      cursor: pointer;

    }
  }

  .login-cart-container {
    // visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 20px;
    position: relative;


    a:first-child {
      visibility: hidden;
    }

    a {
      color: black;
      padding: 10px;
      // display: inline-block;
      // width: 80%;
      margin: 0 20px;
      border-bottom: 1px solid transparent;
    }

    a:hover {
      // background-color: white;
      border-bottom: 1px solid black;
    }

    i {
      font-size: 36px;
      cursor: pointer;
    }

    .cart-amount {
      position: absolute;
      top: -5px;
      right: 20px;
      font-size: 14px;
      background-color: red;
      color: white;
      height: 20px;
      width: 20px;
      text-align: center;
      line-height: 21px;
      border-radius: 50%;
      cursor: pointer;
    }


  }


} /*end nav*/

.search-box-container.open {
  display:flex;
  justify-content: space-around;
  margin: 0 auto;
  padding: 15px 20px;
  background: white;
  border-bottom: 1px solid $light-gray;
}

.search-box-container {
  display: none;

  .search-box {
    width: 70%;
    padding: 7px 15px;
    font-size: 20px;
  }

  button.btn {
    font-size: 20px;
    min-width: 20%;
  }
}

ul.open {
  height: auto;
}

.main-nav {
  width: 100%;
  overflow: hidden;
  height: 0;
  background: white;

  li {
    float: none;
    text-align: center;
    width: 100%;
    margin: 0;

    a {
      color: black;
      padding: 10px;
      border-bottom: 1px solid black;
      display: block;
      margin: 0;
    }

    a:hover {
      background-color: #f5f5f5;
    }
  }
}


/* ============================================
                  HOME PAGE
============================================ */

.product-main {
  width: 90%;
  background: #fff;
  margin: 5% auto;

  .product-img {
    width: 100%;
    background: lime;
    position: relative;
    overflow: hidden;

    img {
      display: block;
      margin: 0 auto;
      width: 100%;
      height: auto;
      margin: -16% auto;        //'crops' image to make narrow
      cursor: pointer;
    }
  }

  .product-info {
    padding: 15px 20px;
    position: relative;

    h2.desc {
      font-size: 16px;
      color: #000;
      margin-bottom: 5px;
    }

    p.price {
      font-size: 14px;
      color: #888888;
    }

    .atc-home {
      position: absolute;
      right: 20px;
      top: 20px;
    }
  }
}

/* ============================================
                  SEARCH PAGE
============================================ */


.search-main {
  h1 {
    font-size: 50px;
    text-align: center;
  }
}


/* ============================================
                PRODUCT PAGE
============================================ */

.product-single {

  width: 100%;
  background-color: #fff;

  img {
    width: 100%;
  }

  .details {
    padding: 20px;

    h2.desc {
      font-size: 20px;
      color: #000;
      margin-bottom: 5px;
    }

    p.productPrice {
      font-size: 16px;
      color: rgb(54, 54, 54);
    }

    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      display: block;
      width: 100%;
      font-size: 20px;
      padding: 10px 20px;
      margin: 10px 0;
      border-radius: 0;
      border: 1px solid gray;
      background-color: white;
    }

    input {
      width: 31%;
      font-size: 20px;
      padding: 10px;
      text-align: center;
      margin: 0 10px 10px 0;
      border: 1px solid gray;
    }

    .btn {
      font-size: 20px;
      width: 65%;
      padding: 10px;
      border: 1px solid gray;
      float: right;
    }
  }

  h3 {
    padding: 10px;
    font-size: 24px;
  }

  .description {
    padding: 20px;
  }
}


/* ============================================
                  CART PAGE
============================================ */
.pad {
  padding: 0px 10px;
}
.cart-main {
  background-color: #fff;
  padding: 10px;
  margin: 10px;

  .cart-user-m {

    .cart-product-info-m {
      display: flex;
      align-items: center;
      // background: red;

      .cart-product-info-container-m {
        display: flex;
        align-items: center;

        img {
          height: 50px;
          padding-right: 10px;
        }
      }
    }

    .cart-product-qty-m {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // background: blue;

      div input {
        padding: 7px;
        width: 35px;
        text-align: center;
      }
    }

    .cart-product-itemtotal-m {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // background: yellow;
      // padding-left: 5px;
    }


  }

  .total-disc-ship {

    .d {
      display: flex;
      justify-content: space-between;
      padding: 5px 0;

      div {
        // display: flex;
        // justify-content: flex-end;
        text-align: right;
      }

      .aleft {
        text-align: left;
      }
    }
  }
}

/* ============================================
                  ADMIN PAGE
============================================ */

.admin-main {
  padding: 10px;

  h1 {
    text-align: center;
  }

  .admin-items {

    .admin-filter {
      input {
        width: 100%;
        padding: 7px;
      }
    }

    .admin-products {
      position: relative;
      display: flex;
      align-items: center;
      margin: 10px 0;

      .edit-buttons {
        position: absolute;
        right: 0;
        bottom: 50%;
        transform: translate(0,50%);
      }
    }

    .admin-products:hover {
      background-color: #d9d9d9;
    }
  }

  .admin-edit {

    input, textarea {
      width: 100%;
      padding: 7px;
    }
    h3 {
      padding: 10px 0;
    }
  }
}



.login-main {
  width: 500px;
  height: 300px;
  background: #fff;
  margin: 5% auto;
  // text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  img {
    width: 350px;
  }
}

